import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import CardApp from "./CardApp";
import Auth from "./Auth";

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path={["/login", "/"]}>
          <Auth />
        </Route>
        <Route exact path={["/user"]}>
          <CardApp />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
