import React, { useState } from "react";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import Zoom from "@material-ui/core/Zoom";

function CreateNote(props) {
  const blank = {
    title: "",
    content: "",
  };

  const [textArea, setTextArea] = useState(1);

  const [isExpanded, setIsExpanded] = useState(false);

  const [newPost, setNewPost] = useState({
    ...blank,
  });

  function expand() {
    setIsExpanded(true);
  }

  function increaseArea(event) {
    if (event.key === "Enter") {
      setTextArea((prevValue) => {
        if (prevValue === 21) {
          return prevValue;
        } else {
          return prevValue + 1;
        }
      });
      console.log(textArea);
    }
  }

  function handleChange(event) {
    const { value, name } = event.target;
    setNewPost((prevValue) => {
      return {
        ...prevValue,
        [name]: value,
      };
    });
  }

  function handleSubmit(event) {
    if (newPost.title !== blank.title || newPost.content !== blank.content) {
      props.onSubmit(newPost);
    }

    event.preventDefault();
    setTextArea(0);
    setIsExpanded(false);
    setNewPost({
      ...blank,
    });
  }

  return (
    <div>
      <form className="create-note" onSubmit={handleSubmit}>
        {isExpanded ? (
          <input
            onChange={handleChange}
            name="title"
            placeholder="Title"
            value={newPost.title}
            autoComplete="off"
          />
        ) : null}

        <textarea
          onClick={expand}
          onKeyPress={increaseArea}
          onChange={handleChange}
          value={newPost.content}
          name="content"
          placeholder="Take a note..."
          rows={isExpanded ? textArea : "1"}
        />
        <Zoom in={isExpanded}>
          <Fab type="submit">
            <AddIcon />
          </Fab>
        </Zoom>
      </form>
    </div>
  );
}

export default CreateNote;
