import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

function Login() {
  const [user, setUser] = useState({
    email: "",
    password: "",
  });

  const { email, password } = user;

  function onChange(e) {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  }

  async function onSubmit(e) {
    e.preventDefault();

    const newUser = {
      email: email,
      password: password,
    };

    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const body = JSON.stringify(newUser);
      const res = await axios.post("/users/login", body, config);
      if (res.data) {
        window.location = "/user";
      }
    } catch (err) {
      alert(err.response.data);
    }
  }

  return (
    <form onSubmit={onSubmit}>
      <h1 className="h3 mb-3 fw-normal">Sign in</h1>
      <input
        type="text"
        name="email"
        id="inputEmail"
        className="form-control"
        value={user.email}
        onChange={onChange}
        placeholder="Email address"
        required
        autoFocus
      />
      <input
        type="password"
        name="password"
        id="inputPassword"
        onChange={onChange}
        className="form-control"
        placeholder="Password"
        required
        value={password}
      />

      <button className="w-100 btn btn-lg btn-primary sign-in" type="submit">
        Sign in
      </button>
      <Link to="/register">
        <button className="w-100 btn btn-lg btn-primary register">
          Register
        </button>
      </Link>
    </form>
  );
}

export default Login;
