import React, { useState, useEffect } from "react";
import axios from "axios";

import Header from "./Header";
import Footer from "./Footer";
import NoteList from "./NoteList";
import CreateNote from "./CreateNote";

function CardApp() {
  //Retrieving User Profile
  const [userInfo, setUserInfo] = useState("");
  useEffect(() => {
    axios.get("/users/notes").then(function (response) {
      if (response.data === null) {
        window.location = "/";
      }
      setUserInfo(response.data.email);
      setNoteList(response.data.secrets);
    });
  }, []);

  const [noteList, setNoteList] = useState([]);
  async function holdNotes(note) {
    //Assigning Notes to User
    setNoteList((prevValue) => [...prevValue, note]);
    try {
      const upload = {
        email: userInfo,
        arr: [...noteList, note],
      };
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const body = JSON.stringify(upload);
      axios.post("/users/notes", body, config);
    } catch (err) {
      console.log(err.response.data);
    }
  }
  async function deleteNote(id) {
    //Deleting Note
    setNoteList((prevValue) => {
      return prevValue.filter((value, index) => {
        return index !== id;
      });
    });
    try {
      const upload = {
        email: userInfo,
        arr: noteList.filter((value, index) => {
          return index !== id;
        }),
      };
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      const body = JSON.stringify(upload);
      axios.post("/users/notes", body, config);
    } catch (err) {
      console.log(err.response.data);
    }
  }

  return (
    <div>
      <Header />
      <CreateNote onSubmit={holdNotes} />
      <NoteList noteArray={noteList} delete={deleteNote} />
      <Footer />
    </div>
  );
}

export default CardApp;
