import React from "react";
import BackspaceIcon from "@material-ui/icons/Backspace";

function Note(props) {
  function destroy() {
    props.delete(props.id);
  }
  return (
    <div className="note">
      <h1>{props.title}</h1>
      <p>{props.content}</p>
      <button onClick={destroy}>
        <BackspaceIcon />
      </button>
    </div>
  );
}
export default Note;
